.inputGroup{display:flex;align-items:center;gap:.5rem;margin-bottom:1rem}.label{font-size:.95rem;font-weight:500}.label.light{color:var(--secondary)}.label.dark{color:var(--secondary2)}.input,.select{flex:1;padding:.5rem;border-radius:8px;border:1px solid #ccc;font-size:1rem;box-sizing:border-box;transition:border-color .2s}.input.light,.select.light{background-color:var(--white);color:var(--black)}.input.dark,.select.dark{background-color:var(--dark2);color:var(--white)}.select{padding:.4rem;max-width:80px;height:40px;appearance:none}.authButton{padding:.75rem 1rem;background-color:var(--primary);color:#fff;font-weight:bold;border:none;border-radius:8px;cursor:pointer}.authButton.hidden{display:none}