.loginPage{margin:0}.loginHeader{height:150px;display:flex;justify-content:space-between;align-items:center}.loginHeader.light{background-color:var(--surface)}.loginHeader.dark{background-color:var(--dark)}.loginContainer{width:100%;max-width:1200px;margin-left:auto;margin-right:auto;padding-left:15px;padding-right:15px;box-sizing:border-box;display:flex;flex-direction:column;justify-content:center;align-items:center}.loginTitle{display:flex;justify-content:space-between;align-items:center;width:100%}.loginTitle h2{margin:0;font-size:1.5rem}.loginTitle h2.light{color:var(--on-surface)}.loginTitle h2.dark{color:var(--surface)}.loginTitle p{margin:0;font-size:1rem;opacity:.7}.loginTitle p.light{color:var(--secondary)}.loginTitle p.dark{color:var(--secondary2)}