.footer{width:100%;padding:40px 20px}.footer.dark{background-color:var(--dark1);color:var(--surface)}.footer.light{background-color:var(--white);color:var(--on-surface)}.footer .top{display:flex;flex-direction:row;justify-content:space-between;flex-wrap:wrap;max-width:1200px;margin:auto;gap:2rem}.footer .left{flex:1;min-width:250px}.footer .left .logo{font-size:24px;font-weight:700;margin-bottom:10px}.footer .left .title{font-size:18px;font-weight:600;margin-bottom:8px}.footer .left p{margin:5px 0}.footer .columns{display:flex;flex:2;justify-content:space-between;flex-wrap:wrap;gap:2rem}.footer .columns div{min-width:120px}.footer .columns div h4{font-weight:600;margin-bottom:8px}.footer .columns div li{font-size:14px;line-height:1.8;list-style:none}.bottom{width:100%}.bottom.dark{background-color:var(--dark-hover);color:var(--secondary2)}.bottom.light{background-color:var(--surface);color:var(--secondary2)}.bottom .container{display:flex;justify-content:space-between;align-items:center;max-width:1200px;margin:0 auto}.bottom .socials{display:flex;gap:10px}