.button{background-color:var(--primary);color:var(--white);padding:16px 24px;border-radius:90px;border:none;font-weight:700;cursor:pointer;text-align:center;transition:transform .3s ease}.button.activeButton{background-color:var(--primary);color:var(--white)}.button.inactiveButton{background:none}.button.inactiveButton.light{color:var(--on-surface)}.button.inactiveButton.dark{color:var(--white)}