;color:inherit;margin:0}.keyContainer{position:relative;display:flex;flex-direction:row;justify-content:center;align-items:center;gap:0}.keyIcon{z-index:1;width:24px;height:24px}.greenDot{position:absolute;bottom:-5px;width:36px;height:36px;z-index:0;background-color:var(--success);border-radius:50%}.urlText{font-size:14px}.urlText.light{color:var(--on-surface)}.urlText.dark{color:var(--surface)}.greenLink{color:var(--success);text-decoration:none}