.banner{width:100%;height:140px;padding:0;display:flex;flex-direction:row;justify-content:center;align-items:center}.banner.light{background:var(--decorative)}.banner.dark{background-color:var(--dark2)}.container{max-width:1200px;margin:0 auto;padding:0 20px;display:flex;flex-direction:row;justify-content:space-between;align-items:center;width:100%}.leftContent{flex:2;display:flex;flex-direction:column;justify-content:flex-start;align-items:center;width:100%}.rightContent{flex:1;display:flex;flex-direction:row;justify-content:flex-end;align-items:center;width:100%}.title{font-size:32px;font-weight:700;line-height:40px;color:var(--surface);margin:0;text-align:left;width:100%}.description{font-size:16px;font-weight:400;line-height:24px;color:var(--surface);margin:0;text-align:left;width:100%}.footerButton{background-color:var(--white) !important;color:var(--on-surface) !important;padding:16px 24px;border-radius:90px;border:none;font-weight:700;cursor:pointer;text-align:center}